import { Component, OnInit } from '@angular/core';

import {Router,ActivatedRoute}  from '@angular/router';
import {OperationService}  from '../operation.service';
@Component({
  selector: 'app-bankview',
  templateUrl: './bankview.component.html',
  styleUrls: ['./bankview.component.scss']
})
export class BankviewComponent implements OnInit {

  x:any;
  constructor(private active:ActivatedRoute,private operation:OperationService,private router:Router) { }
 

  ngOnInit() {
  	this.active.params.subscribe(params=>{
        this.operation.Edit(params['id'],"bankview").subscribe(res=>{
           this.x=res[0];
             // put values in the form
             console.log(res)
     
         })
  });
  }

}
